.top-navbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: var(--top-nav-gap);
  padding: 1%;
  height: var(--top-nav-height);
  backdrop-filter: blur(8.7px);
  -webkit-backdrop-filter: blur(8.7px);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999999;
  box-shadow: 0 4px 30px rgb(255 255 255 / 60%);
}

.top-navbar img {
  max-height: 100%;
  width: auto;
  border-radius: 50%;
}
.nav-temple-logo-container {
  height: -webkit-fill-available;
}

@media (prefers-color-scheme: dark) {
    .top-navbar{
        background: rgba(0, 0, 0, 0.73);
    }
}
@media (prefers-color-scheme: light) {
    .top-navbar{
        background: rgba(255, 255, 255, 0.73);
    }
}
